.test {
  margin-top: 80px;
  max-width: 1160px;
  height: 613px;
  border: 1px solid #232323;
  border-radius: 70px;
  background-color: #ACC7F6;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  box-sizing: border-box;
  padding: 80px 8.5vw;
}

 @media screen and (min-width: 1440px) {
  .test {
    box-sizing: content-box;
    padding: 0 273px;
  }
 }

 @media screen and (max-width: 376px) {
  .test {
    margin-top: 60px;
    padding: 0 37px;
    height: 500px;
    border-radius: 38px;
  }
 }
